import * as React from "react"
import { Link } from "gatsby"

declare const __PATH_PREFIX__: unknown

type Props = React.PropsWithChildren & {
  location: Location
  title: string
}

const Layout: React.FC<Props> = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath
  const amountOfSlashes = location.pathname.match(/\//g)?.length ?? 0
  const isFirstOrSecondLevel = amountOfSlashes < 3

  const header = isFirstOrSecondLevel
    ? <h1 className="main-heading">
      <Link to="/">{title}</Link>
    </h1>
    : <Link className="header-link-home" to="/">
      {title}
    </Link>

  return (
    <div className="global-wrapper" data-is-root-path={isRootPath}>
      <header className="global-header">{header}</header>
      <main>{children}</main>
      <footer>
        © {new Date().getFullYear()}, Construït amb
        {` `}
        <a href="https://www.gatsbyjs.com">Gatsby</a>
      </footer>
    </div>
  )
}

export default Layout
